body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app {
  text-align: center;
}
.app-header {
  background-color: darkblue;
  left: 0;
  padding-top: 20px;
  position: relative;
  right: 0;
  top: 0;
  z-index: 5000;
}
h1 {
  color: white;
  font-size: 26px;
}
ul {
  list-style: none;
}
.app-header ul li {
  color: white;
  display: inline-block;
  margin: 20px;
}
a {
  text-decoration: none;
}
a:visited {
  color: darkgray;
}
a:hover,
a:active {
  color: lightblue;
  font-weight: 500;
}
a:focus {
  outline: 0;
}

.ingredients-list {
    list-style: none;
    max-height: 35vh;
    overflow-y: auto;
    padding: 10px 40px;
    text-align: left;
}
ul li {
    display: block;
    margin: 0;
}
.instructions-list {
    list-style: none;
    max-height: 35vh;
    overflow-y: auto;
    padding: 10px 40px;
    text-align: left;
}
ul li {
    display: block;
    margin: 0;
}
.main-recipe-form {
    margin: 0 auto;
    max-width: 750px;
}
label {
    display: block;
    margin: 15px 0 0 15px;
    text-align: left;
}
input {
    border: 1px solid #ccc;
    box-shadow: 2px 2px 2px 0 #ccc;
    display: block;
    font-size: 16px;
    height: 30px;
    margin: 0 15px;
    text-align: left;
}
.ingredient {
    border: none;
    box-shadow: none;
}
.ingredients input {
    display: inline-block;
}
.qty,
.unit {
    width: 50px;
}
.instruction {
    border: none;
    box-shadow: none;
}

.instructions input {
    display: inline-block;
    width: 70%;
}
